.box_info {
	padding-bottom: 73px;
	margin-top: 39px;
	p {
		font-size: 18px;
		line-height: 30px;
		transition: all .5s;
	}
	span {
		float: right;
		cursor: pointer;
		color: #01A19A;
		font-size: 18px;
		padding-bottom: 3px;
		border-bottom: 1px dashed #01A19A;
	}
}

.footer {
	display: flex;
	flex-direction: column;
	overflow: hidden;
	&_top {
		display: flex;
		justify-content: center;
		align-items: center;
		background-color: #F1891D;
		height: 56px;
		@include m991 {
			height: 40px;
		}
		ul {
			display: flex;
			@include m991 {
				justify-content: space-between;
			}
			@include m1025_1200 {
				justify-content: center;
			}
			li {
				margin-right: 40px;
				@include m991 {
					margin-right: auto;
				}
				&:last-child {
					margin-right: 0;
				}
				a {
					color: #ffffff;
					font-size: 18px;
					font-weight: bold;
					@include m991 {
						font-weight: 500;
						font-size: 14px;
					}
				}
			}
		}
	}
	&_bottom {
		height: 200px;
		background-color: #D83942;
		@include m991 {
			height: auto;
		}
		&__wrap {
			display: flex;
			flex-direction: column;
			&-top {
				display: flex;
				flex-wrap: wrap;
				.nav {
					display: flex;
					flex-wrap: wrap;
					align-items: flex-start;
					margin-top: 29px;
					@include m991 {
						flex-direction: column;
						display: block;
						width: 100%;
						>.smart-event {
							display: flex;
							width: 100%;
							justify-content: space-between;

							i {
								color: #ffffff;
								font-size: 12px;
							}
						}
					}
					
					span {
						font-size: 18px;
						font-weight: 500;
						color: #ffffff;
						margin-bottom: 10px;
						text-transform: uppercase;
					}
					ul {
						position: relative;
						padding-top: 30px;
						padding-right: 35px;
						padding-left: 35px;
						border-right: 2px solid #CC3941;
						@include m991 {
							width: 100%;
							padding-right: 0;
							padding-left: 0;
							padding-top: 0;
							margin-bottom: 25px;
							border-right: 0;
							padding-bottom: 5px;
							border-bottom: 2px solid #CC3941;
						}
						@include m1025_1200 {
							padding-right: 10px;
							padding-left: 10px;
						}
						&:last-child {
							column-count: 2;
							padding-right: 0;
						}
						&:first-child {
							padding-left: 0;
						}
						&:nth-child(3) {
							padding-bottom: 30px;
						}
						li {
							line-height: 30px;
							&:last-child {
								margin-bottom: 0;
							}

							@include m991 {
								display: none;
							}

							a {
								color: #ffffff;
								padding: 4px 8px;
								border-radius: 8px;
								
								font-size: 14px;
								@include m991 {
									padding: 4px 0;
								}
								&:hover {
									@extend .active;
									@include m991 {
										background-color: transparent;
										box-shadow: none;
									}
								}
							}
							.active {
								background: #F1891D;
								box-shadow: inset 2px 3px 4px rgba(0, 0, 0, 0.2);
							}
						}
					}
					
				}
				.right {
					display: flex;
					flex-direction: column;
					margin-left: 30px;
					margin-top: 27px;
					@include m991 {
						margin-left: 0;
						margin-top: 0;
					}
					span {
						font-size: 18px;
						font-weight: bold;
						color: #ffffff;
						text-transform: uppercase;
						margin-bottom: 20px;
					}
					ul {
						display: flex;
						flex-wrap: wrap;
						li {
							margin-right: 9px;
							&:last-child {
								margin-right: 0;
							}
							i {
								font-size: 25px;
							}
						}
					}
					ul.card {
						li {
							margin-right: 14px;
							img {
								width: 40px;
							}
						}
					}
					.footer_btn {
						margin: 14px 0;
						@include m991 {
							display: inline-block;
							margin: 0 14px;
						}
						a {
							color: #000000;
							background-color: #ffffff;
							border-radius: 8px;
							padding: 8px 29px;
							font-weight: bold;
							display: inline-block;
						}
					}
				}
				
			}
			&-bottom {
				position: relative;
				bottom: 20px;
				display: flex;
				justify-content: center;
				@include m991 {
					margin-top: 20px;
				}
				span {
					color: #A4161E;
					font-size: 14px;
				}
			}
		}
	}
}

@include m991 {
	.smart_line {
		display: flex;
		align-items: center;
		margin-bottom: 22px;
	}
}


.fluid_footer {
	padding: 0;
	margin: 0;
	@include m991 {
		margin-top: 0 !important;
		padding-top: 0 !important;
		padding-bottom: 0 !important;
		margin-bottom: 0 !important;
	}
}