* {
	padding: 0;
	margin: 0;
	outline: none;
	box-sizing: border-box;
}

li {
	list-style: none;
}

a {
	text-decoration: none;
}

button {
	border: none;
	background-color: transparent;
}

img {
	width: 100%;
	height: auto;
}

em {
	font-style: normal;
}

html {
	height: 100%;
}

body {
	position: relative;
	font-size: 16px;
	font-family: 'Champagne & Limousines';
	line-height: 1;
	min-width: 320px;
	color: #000000;
	overflow-x: hidden;
	background-color: rgba(1, 161, 154, .05);
}

@include mobile {
	.scane {
		display: none;
	}
}
@include mobile {
	.scane {
		display: none;
	}
}

@include m486_768 {
	.scane {
		display: none;
	}
}
@include m1025_1200 {
	.scane {
		display: none;
	}
}

.scane {
	z-index: -2;
}

.container {
	max-width: 1200px;
	margin: 0 auto;
	position: relative;

	@include mobile {
		width: calc(100% - 40px);
	}
	@include m486_768 {
		width: calc(100% - 40px);
	}
	@include m768_991 {
		width: calc(100% - 40px);
	}
	@include m1025_1200 {
		width: calc(100% - 40px);
	}
}

.container_fluid {
	width: 100%;
	padding-top: 65px;
	padding-bottom: 87px;
	margin-top: 87px;
	margin-bottom: 65px;
	position: relative;
	@include mobile {
		padding-bottom: 44px;
		margin-bottom: 37px;
		padding-top: 44px;
		margin-top: 37px;
	}
	@include m486_768 {
		padding-bottom: 44px;
		margin-bottom: 37px;
		padding-top: 44px;
		margin-top: 37px;
	}

	@include m768_991 {
		padding-bottom: 44px;
		margin-bottom: 37px;
		padding-top: 44px;
		margin-top: 37px;
	}
	
	&::before {
		content: "";
		position: absolute;
		top: 0;
		left: 0;
		background-color: #ffffff;
		z-index: -2;
		width: 100%;
		height: 100%;
	}
}

.static {
	position: static;
	z-index: 1;
}

.smart-event {
	display: none;
	pointer-events: none;
	@include mobile {
		display: block;
		pointer-events: visible;
	}
	@include m486_768 {
		display: block;
		pointer-events: visible;
	}

	@include m768_991 {
		display: block;
		pointer-events: visible;
	}
}

.desktop-event {
	display: block;
	@include mobile {
		display: none;
	}
	@include m486_768 {
		display: none;
	}

	@include m768_991 {
		display: none;
	}
}

.hide_anim {
	background-color: #f2faf9;
	position: relative;
	z-index: 10;
	padding-top: 87px;
	// margin-top: 65px;
}

.order_first {
	.container_fluid {
		// padding-bottom: 0;
		margin-bottom: 0;
	}
}

@include m486_768 {
	.grid_smart {
		display: flex;
		flex-direction: column;
	}

	.order_first {
		order: 1;

	}

	.order_center {
		order: 2;

		.container_fluid {
			// padding-top: 0;
			padding-bottom: 0;
			margin-top: 0;

			&::before {
				background-color: transparent;
			}
		}
	}

	.order_last {
		order: 3;
		background-color: #ffffff;
		padding-top: 37px;
	}

	#slider2 {
		margin-top: 28px;
	}
}

