.section_col_2 {
	display: flex;
	flex-wrap: wrap;
	justify-content: space-between;
	overflow: hidden;
	@include m486_768 {
		justify-content: center;
	}

	@include m768_991 {
		justify-content: center;
	}
}
.section_row {
	display: flex;
	flex-direction: column;
	@include m1025_1200 {
		width: 48%;
	}
}

.padd_b {
	margin-bottom: 63px;
	@include m991 {
		margin-bottom: 23px;
	}
}

.image_item {
	margin-bottom: 12px;
	&:last-child {
		margin-bottom: 0;
	}
}

.social_box {
	display: flex;
	align-items: center;
	flex-direction: column;
	@include m991 {
		margin-top: 32px;
	}
	&__header {
		margin-bottom: 23px;
		.title {
			font-size: 18px;
			font-weight: bold;
			text-transform: uppercase;
			@include mobile {
				font-size: 16px;
			}
		}
	}
	&__items {
		display: flex;
		flex-wrap: wrap;
		.social_item {
			padding: 12px;
			background-color: #ffffff;
			border-radius: 8px;
			margin-right: 20px;
			box-shadow: -15px 0px 20px rgba(0, 0, 0, 0.07);
			&:last-child {
				margin-right: 0;
			}
		}
	}
}

.box_mak {
	margin-top: 70px;
	@include mobile {
		margin-top: 32px;
		display: flex;
	}
}

.parallax_r1,
.parallax_r2,
.parallax_r3 {
	position: absolute;
}
.parallax_r1 {
	top: -20px;
	right: -220px;
}
.parallax_r2 {
	top: 68%;
	right: -200px;
	transform: rotate(-45deg) !important;
}
.parallax_r3 {
	top: 85%;
	right: 42%;
	z-index: -1;
}