.convas {
	position: absolute;
	top: 150px;
	left: 0;
	overflow: hidden;
	width: 100%;
	height: 100%;
	max-height: 4512px;
	z-index: -1;
	// margin-top: 120px;
	@include mobile {
		display: none;
	}
	@include m486_768 {
		display: none;
	}

	@include m768_991 {
		display: none;
	}
	@include m1025_1200 {
		display: none;
	}
	@include only_tablet {
		top: 185px;
		max-height: auto;
	}
	svg {
		width: 100%;
		padding: 0 110px;
		height: auto;

		#map {
			vector-effect: "non-scaling-stroke";
			animation: dash 10s linear infinite;
		}

		@-webkit-keyframes dash {
			to {
				stroke-dashoffset: 0;
			}
		}

		@keyframes dash {
			to {
				stroke-dashoffset: 0;
			}
		}

		@include only_tablet {
			padding: 0;
		}

		@include desktop {
			padding: 0;
		}
	}
}

.header_fixed {
	position: fixed !important;
	top: 0;
	left: 0;
	width: 100%;
	.header_logo {
		display: none;
	}
}

.slider_item__wp {
	display: flex;
	flex-wrap: wrap;
}

.slider_item__pic {
	width: 50%;
	@include mobile {
		width: 100%;
	}
	@include m486_768 {
		width: 100%;
	}

	@include m768_991 {
		width: 100%;
	}
}

.slider_item {
	position: relative;
}

.header {
	background-color: #F1891D;
	height: 50px;
	display: flex;
	align-items: center;

	@include mobile {
		height: 40px;
		position: static;
		z-index: 1;
	}

	@include m486_768 {
		height: 40px;
		position: static;
		z-index: 1;
	}

	@include m768_991 {
		height: 40px;
		position: static;
		z-index: 1;
	}

	.container {
		display: flex;
		justify-content: space-between;
		flex-grow: 1;
		align-items: center;

		@include mobile {
			flex-grow: 0;
		}

		@include m486_768 {
			flex-grow: 0;
		}

		@include m768_991 {
			flex-grow: 0;
		}
	}
	&_logo {
		position: absolute;
		top: 0;
		left: 0;
		background-color: $green;
		height: 190px;
		width: 25%;
		z-index: 1;
		display: flex;
		align-items: center;
		justify-content: flex-end;
		border-bottom-right-radius: 10px;
		box-shadow: 20px 25px 30px rgba(0, 0, 0, 0.1);
		@include mobile {
			top: 40px;
			height: 73px;
			width: 50%;
			z-index: -1;
			box-shadow: 10px 15px 15px rgba(0, 0, 0, 0.1);
			border-bottom-right-radius: 35px;
			justify-content: flex-start;
		}
		@include only_tablet {
			width: 15%;
		}
		@include desktop {
			width: 20%;
		}
		@include m320 {
			width: 40%;
		}
		@include m486_768 {
			top: 40px;
			height: 73px;
			width: 40%;
			z-index: -1;
			box-shadow: 10px 15px 15px rgba(0, 0, 0, 0.1);
			border-bottom-right-radius: 35px;
			justify-content: flex-start;
		}

		@include m768_991 {
			top: 40px;
			height: 73px;
			width: 30%;
			z-index: -1;
			box-shadow: 10px 15px 15px rgba(0, 0, 0, 0.1);
			border-bottom-right-radius: 35px;
			justify-content: flex-start;
		}
		&::before {
			content: "";
			position: absolute;
			top: -30px;
			width: 105px;
			height: 200px;
			right: -48px;
			transform: rotate(25deg);
			background-color: $green;
			/* border: 150px solid transparent;
			border-color: $green transparent transparent $green; */
			@include mobile {
				width: 50px;
				height: 80px;
				right: -14px;
				z-index: -1;
			}
			@include m486_768 {
				width: 50px;
				height: 80px;
				right: -14px;
				z-index: -1;
			}

			@include m768_991 {
				width: 50px;
				height: 80px;
				right: -14px;
				z-index: -1;
			}
		}
		&__wrap {
			display: flex;
			flex-direction: column;
			align-items: center;
			position: relative;
			@include mobile {
				flex-direction: row;
			}
			@include m486_768 {
				flex-direction: row;
			}

			@include m768_991 {
				flex-direction: row;
			}
			a {
				display: flex;
				flex-direction: column;
				align-items: center;
				justify-content: center;
				@include mobile {
					flex-direction: row;
				}
				@include m486_768 {
					flex-direction: row;
				}

				@include m768_991 {
					flex-direction: row;
				}
			}
			img {
				margin-bottom: 9px;
				width: 140px;
				@include mobile {
					width: 65px;
					height: 54px;
				}
				@include m320 {
					width: 56px;
				}
				@include m486_768 {
					width: 72px;
					// height: 54px;
				}

				@include m768_991 {
					width: 72px;
				}
			}
			span {
				color: #ffffff;
				text-transform: uppercase;
				@include m320 {
					font-size: 12px;
				}
				@include m486_768 {
					width: 72px;
					font-size: 18px;
				}

				@include m768_991 {
					width: 72px;
					font-size: 18px;
				}
			}
		}
	}
	&_nav {
		display: flex;
		flex-grow: 1;
		justify-content: center;
		@include mobile {
			justify-content: space-between;
		}
		@include m486_768 {
			justify-content: space-between;
		}

		@include m768_991 {
			justify-content: space-between;
		}
		ul {
			display: flex;
			flex-wrap: wrap;
			margin-left: 110px;
			@include mobile {
				margin-left: 0;
				justify-content: space-between;
			}
			@include m320 {
				width: 100%;
			}
			@include m486_768 {
				margin-left: 0;
				justify-content: space-between;
				width: 100%;
			}

			@include m768_991 {
				margin-left: 0;
				justify-content: space-between;
				width: 100%;
			}
			li {
				margin-right: 42px;
				@include m486_768 {
					margin-right: 20px;
				}

				@include m768_991 {
					margin-right: 20px;
				}
				@include mobile {
					margin-right: 20px;
				}
				@include m320 {
					margin-right: 3px;
				}
				&:last-child {
					margin-right: 0;
				}
				a {
					font-size: 18px;
					color: #ffffff;
					@include mobile {
						font-size: 14px;
						font-weight: bold;
					}
					@include m320 {
						font-size: 12px;
					}
					@include m486_768 {
						font-size: 16px;
					}

					// @include m768_991 {
					// 	font-size: 14px;
					// }
				}
			}
		}
	}
	&_right {
		display: flex;
		flex-wrap: wrap;
		@include mobile {
			position: absolute;
			bottom: -62px;
			right: 0;
		}
		@include m486_768 {
			position: absolute;
			bottom: -62px;
			right: 0;
		}

		@include m768_991 {
			position: absolute;
			bottom: -62px;
			right: 0;
		}
		&__item {
			position: relative;
			padding: 0 13px;
			&:first-child {
				border-right: 1px solid rgba(216, 216, 216, .15);
			}
			@include mobile {
				&:last-child {
					padding: 0 0 0 13px;
				}
			}
			@include m486_768 {
				&:last-child {
					padding: 0 0 0 13px;
				}
			}

			@include m768_991 {
				&:last-child {
					padding: 0 0 0 13px;
				}
			}
			span {
				position: absolute;
				top: -4px;
				right: 3px;
				background-color: $green;
				color: #ffffff;
				width: 24px;
				height: 24px;
				border-radius: 50%;
				display: flex;
				align-items: center;
				justify-content: center;
				pointer-events: none;
			}
			.icon-shop {
				font-size: 32px;
				color: #ffffff;
				@include mobile {
					color: $orange !important;
				}
				@include m486_768 {
					color: $orange !important;
				}

				@include m768_991 {
					color: $orange !important;
				}
			}
			.icon-scale {
				font-size: 32px;
				color: #ffffff;
				@include mobile {
					color: $orange;
				}
				@include m486_768 {
					color: $orange !important;
				}

				@include m768_991 {
					color: $orange !important;
				}
			}
			.icon-burger {
				font-size: 32px;
				color: #000000;
				@include m991 {
					padding: 10px 0;
				}
			}
		}
	}
}

@include m991 {

	.smart_push {
		display: none;
		position: fixed;
		width: 100%;
		height: 100%;
		top: 40px;
		background-color: #ffffff;
		padding-left: 20px;
		padding-right: 20px;
		margin-left: -20px;
		margin-right: -20px;
		z-index: 9999;
		flex-direction: column;
		overflow-y: auto;
		padding-bottom: 40px;
		-webkit-overflow-scrolling:touch;
		-webkit-box-orient: vertical;
		&__close {
			position: absolute;
			top: 10px;
			right: 5px;
			z-index: 10;
			padding: 10px;
		}
		&__bottom {
			display: flex;
			justify-content: center;
			flex-wrap: wrap;
			padding: 0 20px;
			height: 77px;
			align-items: center;
			background-color: $orange;
			margin-right: -20px;
			margin-left: -20px;
			&-item {
				position: relative;
				margin-right: 50px;
				&:last-child {
					margin-right: 0;
				}
				span {
					position: absolute;
					top: -4px;
					right: -6px;
					background-color: $green;
					color: #ffffff;
					width: 24px;
					height: 24px;
					border-radius: 50%;
					display: flex;
					align-items: center;
					justify-content: center;
					pointer-events: none;
				}
				i {
					font-size: 30px;
					&::before {
						color: #ffffff !important;
					}
				}
			}
		}
		.wrap_search {
			order: 1;
			&__form {
				width: 100%;
				.desktop-event {
					display: none !important;
				}
				.field-set {
					width: 100%;
					.field {
						width: 100%;
						@include m991 {
							display: block;
						}
					}
				}
			}
		}
		.wrap_nav {
			order: 0;
			justify-content: flex-start;
			ul {
				// flex-direction: column;
				display: block;
				border-top: 0;
				margin-top: 13px;
				li {
					display: block;
					margin-right: 0;
					margin-bottom: 24px;
					&:last-child {
						margin-bottom: 0;
					}
					a {
						font-size: 20px;
					}
				}
			}
		}
		> .smart-event {
			// height: 249px;
			order: 2;
			img {
				background-size: 50%;
				margin-bottom: 15px;
				display: block;
				margin-top: 22px;
			}
		}
	}
}

.wrap_search {
	display: flex;
	justify-content: flex-end;
	margin: 21px 0;
	@include m991 {
		margin: 0;
		display: block;
		padding-top: 21px;
	}
	&__form {
		display: flex;
		flex-wrap: wrap;
		align-items: center;
		@include m991 {
			display: block;
		}
		span {
			font-size: 18px;
			color: #A9A9A9;
			margin-right: 43px;
		}
	}
}

/* Навигация */

.wrap_nav {
	display: flex;
	justify-content: flex-end;
	@include m991 {
		display: block;
	}
	ul {
		display: flex;
		flex-wrap: wrap;
		padding-top: 10px;
		border-top: 1px solid #DEDEDE;
		li {
			margin-right: 100px;
			&:last-child {
				margin-right: 0;
			}
			a {
				font-size: 24px;
				color: #000000;
				text-transform: uppercase;
				font-weight: 500;
				&:hover {
					color: $green;
				}
			}
		}
	}
}

/* / Навигация */

.field-set {
	position: relative;
}

.field {
	background-color: #FFFFFF;
	border: 1px solid #E9E9E9;
	border-radius: 8px;
	padding: 15px 50px 15px 20px;
}

.search-btn {
	cursor: pointer;
	position: absolute;
	top: 50%;
	margin-top: -13px;
	right: 10px;
}

.icon-search {
	color: $green;
	font-size: 26px;
}

// Слайдер в шапке

.wrap-box-width {
	display: flex;
	flex-wrap: wrap;
	justify-content: space-between;
}

.slider_box {
	margin-top: 48px;
	margin-bottom: 66px;
	position: relative;
	@include mobile {
		margin-top: 100px;
	}
	@include m486_768 {
		margin-top: 100px;
	}

	@include m768_991 {
		margin-top: 100px;
	}
	.slider_right {
		display: flex;
		align-items: flex-start;
		justify-content: space-between;
		flex-direction: column;
		height: 607px;
		width: 100%;
		@include mobile {
			height: 345px;
			align-items: center;
		}
		@include m486_768 {
			height: 345px;
			align-items: center;
		}

		@include m768_991 {
			height: 345px;
			align-items: center;
		}
		// width: 50%;
		// background: url(../img/header-slider_main.jpg) no-repeat;
		.right_box {
			width: 50%;
			height: 100%;
			display: flex;
			justify-content: space-between;
			flex-direction: column;
			align-items: center;
			@include mobile {
				width: 100%;
			}
			@include m486_768 {
				width: 100%;
			}

			@include m768_991 {
				width: 100%;
			}
		}
		&__images {
			position: absolute;
			width: 100%;
			height: 100%;
			z-index: 1;
			transition: all 0.5s;
		}
		&__header {
			display: flex;
			flex-direction: column;
			align-items: center;
			margin-top: 37px;
			position: absolute;
			top: 40px;
			left: 130px;
			z-index: 10;
			@include m991 {
				left: 0;
				width: 100%;
			}
			.title {
				font-size: 60px;
				font-weight: bold;
				text-transform: uppercase;
				color: $text-color;
				margin-bottom: 12px;
				@include mobile {
					text-align: center;
					font-size: 36px;
				}
				@include m486_768 {
					text-align: center;
					font-size: 36px;
				}

				@include m768_991 {
					text-align: center;
					font-size: 36px;
				}
			}
			span {
				color: $text-color;
				font-size: 30px;
				margin-bottom: 20px;
				@include mobile {
					font-size: 18px;
				}
				@include m486_768 {
					font-size: 18px;
				}

				@include m768_991 {
					font-size: 18px;
				}
			}
			&-btn {
				background-color: #ffffff;
				font-size: 18px;
				color: $green;
				font-weight: bold;
				padding: 16px 56px;
				border-radius: 8px;
				text-transform: uppercase;
				box-shadow: 15px 12px 25px rgba(0, 0, 0, 0.1);
				@include mobile {
					font-size: 14px;
					padding: 10px 32px;
				}
				@include m486_768 {
					font-size: 14px;
					padding: 10px 32px;
				}

				@include m768_991 {
					font-size: 14px;
					padding: 10px 32px;
				}
			}
		}
		&__smart-nav {
			position: absolute;
			bottom: -30px;
			ul {
				display: flex;
				flex-wrap: wrap;
				li {
					margin-right: 8px;
					&:last-child {
						margin-right: 0;
					}
					button {
						text-indent: -9999em;
						background-color: #C4C4C4;
						width: 12px;
						height: 12px;
						border-radius: 50%;
						display: block;
					}
				}
				li.slick-active {
					button {
						background-color: $green;
					}
				}
			}
		}
		&__bottom {
			display: flex;
			flex-direction: column;
			margin-bottom: 29px;
			align-items: center;
			position: absolute;
			bottom: 25px;
			left: 240px;
			z-index: 2;
			@include m991 {
				display: none;
			}
			.slider_btn {
				color: $text-color;
				font-weight: bold;
				font-family: $Gilroy;
				margin-right: 6px;
				cursor: pointer;
			}
			.slider_progress {
				margin-top: 16px;
				position: relative;
				width: 104px;
				margin-left: 7px;
				margin-right: 13px;
				height: 2px;
				border-radius: 5px;
				overflow: hidden;
				&__box {
					position: absolute;
					top: 0;
					left: 0;
					width: 100%;
					z-index: 0;
					height: 2px;
					background-color: rgba(1, 161, 154, 0.7);
				}
				&__loaded {
					position: absolute;
					top:0;
					left:0;
					width: 33.333%;
					height: 2px;
					background-color: #02746F;
					z-index: 1;
					border-radius: 5px;
					transition: all 0.5s;
				}
			}
		}
		.slick-list {
			width: 100%;
			height: 607px;
			@include m991 {
				height: auto;
			}
		}
		.slick-track {
			// width: 100% !important;
			height: 607px;
			@include mobile {
				height: 345px;
			}
			@include m486_768 {
				height: 345px;
			}

			@include m768_991 {
				height: 345px;
			}
			.slick-slide {
				// width: 100% !important;
			}
			img {
				height: 607px;
			}
		}
	}
}

// Табы на главной странице

.wrap_tabs {
	display: flex;
	flex-wrap: wrap;
	justify-content: center;
	@include mobile {
		flex-direction: column;
		align-items: center;
		margin-top: 14px;
		margin-bottom: 18px;
	}
	@include m486_768 {
		flex-direction: column;
		align-items: center;
		margin-top: 14px;
		margin-bottom: 18px;
	}

	@include m768_991 {
		flex-direction: row;
		align-items: center;
		margin-top: 14px;
		margin-bottom: 18px;
	}
	&__item {
		overflow: hidden;
		cursor: pointer;
		font-size: 30px;
		font-weight: bold;
		padding: 16px 28px;
		text-transform: uppercase;
		margin-right: 6px;
		@include mobile {
			font-size: 24px;
			padding: 12px 25px;
		}
		@include m486_768 {
			font-size: 24px;
			padding: 12px 25px;
		}

		@include m768_991 {
			font-size: 24px;
			padding: 12px 25px;
		}
		&:last-child {
			margin-right: 0;
		}
		&:hover {
			@extend .tab_active
		}
	}
	.tab_active {
		position: relative;
		color: $green;
		&::before {
			content: "";
			position: absolute;
			top: 0;
			left: 0;
			height: 100%;
			width: 100%;
			background-size: 50px;
			background: url(../img/header-line.webp) repeat-x;
			z-index: -1;
		}
	}
}

