.section_header {
	display: flex;
	justify-content: center;
	&__title {
		overflow: hidden;
		font-size: 30px !important;
		font-weight: 500;
		padding: 16px 28px;
		text-transform: uppercase;
		margin-right: 6px;
		position: relative;
		color: $green;
		z-index: 1;
		border-radius: 8px;

		@include mobile {
			font-size: 24px !important;
			padding: 12px 25px;
		}

		@include m320 {
			font-size: 20px !important;
		}

		&::before {
			content: "";
			position: absolute;
			top: 0;
			left: 0;
			height: 100%;
			width: 100%;
			background-size: 50px;
			background: url(../img/header-line.webp) repeat-x;
			z-index: -1;
		}
	}
}

.parallax_x1 {
	position: absolute;
	top: 28px;
	right: 4%;
	width: 102px;
	height: 46px;
}