@mixin desktop {
	@media (min-width: ($mediaWidthDesktop)) {
		@content;
	}
}

@mixin tablet {
	@media (max-width: ($mediaWidthTablet)) {
		@content;
	}
}

@mixin only_tablet {
	@media (min-width: ($mediaWidthTabletOnly_min)) and (max-width: ($mediaWidthTabletOnly_max)) {
		@content;
	}
}

@mixin more_mobile {
	@media (min-width: ($mediaWidthMoreMobile)) {
		@content;
	}
}

@mixin mobile {
	@media (max-width: ($mediaWidthMoreMobile)) {
		@content;
	}
}

@mixin m320 {
	@media (max-width: 320px) {
		@content;
	}
}

@mixin m486_768 {
	@media (min-width: 486px) and (max-width: 768px) {
		@content;
	}
}

@mixin m768_991 {
	@media (min-width: 768px) and (max-width: 991px) {
		@content;
	}
}

@mixin m992_1024 {
	@media (min-width: 991px) and (max-width: 1024px) {
		@content;
	}
}

@mixin m1025_1200 {
	@media (min-width: 1024px) and (max-width: 1200px) {
		@content;
	}
}

@mixin m991 {
	@media (max-width: 991px) {
		@content;
	}
}