.wrap_slider-card {
	display: flex;
	flex-direction: column;
	&__nav {
		display: flex;
		align-items: center;
		justify-content: space-between;
		margin-bottom: 14px;
		@include m991 {
			order: 1;
			position: static;
			z-index: 1;
			justify-content: center;
		}
		.icon {
			font-size: 10px;
			@include mobile {
				font-size: 8px;
			}
		}
		.card__nav_btn {
			cursor: pointer;
			padding: 7px 18px;
			border-radius: 8px;
			box-shadow: 5px 10px 25px rgba(0, 0, 0, 0.2);
		}
		.card__nav_btn-left {
			background-color: $green;
			margin-right: 9px;
		}
		.card__nav_btn-right {
			background-color: #ffffff;
		}
	}
	&__items {
		display: flex;
		flex-wrap: wrap;
		justify-content: space-between;
		.slick-list {
			padding-bottom: 45px;
			margin-bottom: -45px;
			padding-left: 35px;
			margin-left: -35px;
			// padding-right: 35px;
			margin-right: -20px;
			@include m991 {
				padding-bottom: 25px;
				margin-bottom: -25px;
				margin-right: -9px;

			}
		}
		.slick-track {
			width: 100%;
			display: flex;
			flex-wrap: wrap;
			justify-content: space-between;
			margin: 0;
			&::before, &::after {
				display: none;
			}
			.slick-slide {
				margin-right: 20px;
				@include m991 {
					margin-right: 9px;
				}
			}
			@include m991 {
				flex-wrap: unset;
				margin-bottom: 18px;
				justify-content: flex-end;
				display: flex;
				
			}
		}
	}
}

// Карточки

.card_item {
	width: 285px;
	position: relative;
	background-color: #ffffff;
	border-radius: 8px;
	box-shadow: 0px 15px 40px rgba(0, 0, 0, 0.08);
	padding: 20px 20px 35px 20px;
	@include mobile {
		padding: 12px 12px 20px 12px;
		width: 168px;
	}
	&:not(.slick-active) {
		opacity: 0;
		transition: all 0.5s;
	}
	&__top {
		cursor: pointer;
		position: absolute;
		top: 20px;
		right: 20px;
		.icon-scale {
			color: #C0C0C0;
			font-size: 20px;
		}
		&:hover {
			.icon-scale {
				color: $green;
			}
		}
	}
	&__preview {
		display: flex;
		justify-content: center;
		align-items: flex-start;
		margin-top: 7px;

		@include mobile {
			img {
				width: 140px;
				margin-top: 20px;
			}
		}
	}
	&__bottom {
		margin-top: 34px;
		.text {
			font-size: 18px;
			line-height: 24px;
			@include mobile {
				font-size: 16px;
			}
		}
		&_buy {
			display: flex;
			align-items: center;
			justify-content: space-between;
			margin-top: 24px;
			.price_box {
				display: flex;
				align-items: center;
				&__right {
					margin-left: 19px;
					width: 45px;
					height: 45px;
					background-color: #F5F5F5;
					color: $red;
					border-radius: 50%;
					display: flex;
					justify-content: center;
					align-items: center;
					font-size: 18px;
					font-weight: bold;
					@include mobile {
						margin-left: 9px;
						width: 35px;
						height: 35px;
						font-size: 16px;
					}
				}
			}
			.price {
				font-size: 30px;
				font-weight: bold;
				@include mobile {
					font-size: 24px;
				}
				@include m320 {
					font-size: 20px;
				}
				em {
					font-size: 16px;
					@include mobile {
						font-size: 14px;
					}
				}
			}
			.buy {
				width: 45px;
				height: 45px;
				display: flex;
				justify-content: center;
				align-items: center;
				background-color: $green;
				color: #ffffff;
				border-radius: 50%;
				box-shadow: 5px 8px 15px rgba(0, 0, 0, 0.18);
				@include mobile {
					width: 35px;
					height: 35px;
				}
				i {
					font-size: 24px;
					@include mobile {
						font-size: 18px;
					}
				}
			}
		}
	}
}
.position {
	position: relative;
}
.px_b1 {
	position: absolute;
	top: -70px;
	left: -15%;
	z-index: -1;
}

.px_b2 {
	position: absolute;
	top: -40px;
	left: 69%;
	z-index: -1;
}