@font-face {
	font-family: 'Champagne & Limousines';
	src: url('../fonts/ChampagneLimousines/ChampagneLimousines-Italic.eot');
	src: local('Champagne & Limousines Italic'), local('Champagne&Limousines-Italic'),
		url('../fonts/ChampagneLimousines/ChampagneLimousines-Italic.eot?#iefix') format('embedded-opentype'),
		url('../fonts/ChampagneLimousines/ChampagneLimousines-Italic.woff') format('woff'),
		url('../fonts/ChampagneLimousines/ChampagneLimousines-Italic.ttf') format('truetype');
	font-weight: normal;
	font-style: italic;
}

