.grid_container {
	display: grid;
	grid-template-columns: 1fr 1.1fr 1.1fr 1fr 1fr 1fr;
	grid-template-rows: 1fr 1fr;
	gap: 20px 20px;
	position: relative;
	z-index: 0;
	margin-bottom: 70px;
	grid-template-areas:
		"item_1 item_1 item_2 item_2 item_5 item_5"
		"item_3 item_3 item_4 item_4 item_5 item_5";
	@include mobile {
		gap: 0;
		// grid-template-columns: 1fr 1fr 1fr 1fr;
		// grid-template-rows: 1fr 1fr 1fr;
		// grid-template-areas: "item_1 item_1 item_2 item_2"
		// "item_3 item_3 item_4 item_4"
		// "item_5 item_5 item_5 item_5";
		margin-bottom: 0;
		display: flex;
		flex-wrap: wrap;
		justify-content: space-between;
	}
	@include m486_768 {
		gap: 0;
		margin-bottom: 0;
		display: flex;
		flex-wrap: wrap;
		justify-content: center;
	}

	@include m768_991 {
		gap: 0;
		margin-bottom: 0;
		display: flex;
		flex-wrap: wrap;
		justify-content: center;
	}
	&::after {
		content: "";
		position: absolute;
		z-index: -1;
		top: 15px;
		right: 32.333%;
		width: 261px;
		height: 148px;
		background: url(../img/bg_shadow.webp) no-repeat;
		@include m991 {
			display: none;
		}
	}
	&::before {
		content: "";
		position: absolute;
		z-index: -1;
		top: 53%;
		right: 45%;
		width: 261px;
		height: 148px;
		background: url(../img/bg_shadow.webp) no-repeat;
		@include m991 {
			display: none;
		}
	}

	.item_1, .item_2, .item_3, .item_4 {
		height: 275px;
		@include mobile {
			height: 130px;
			width: 46%;
		}
		@include m486_768 {
			height: 185px;
			width: 46%;
		}

		@include m768_991 {
			height: 265px;
			width: 46%;
		}
		img {
			height: 100%;
		}
	}
	.item_1 {
		grid-area: item_1;
		position: relative;
		&::after {
			content: "";
			position: absolute;
			z-index: -1;
			top: 15px;
			left: 20px;
			width: 261px;
			height: 148px;
			background: url(../img/bg_shadow.webp) no-repeat;
			@include m991 {
				display: none;
			}
		}
		
	}

	.item_2 {
		grid-area: item_2;
		.grid_item__abs {
			background-color: rgba(241, 137, 29, 0.9);
			box-shadow: 0 0 0 0.1px rgba(241, 137, 29, 0.9);
		}
	}

	.item_5 {
		grid-area: item_5;
		//margin-top: 50px;
		height: 575px;
		display: flex;
		align-items: flex-end;
		@include m991 {
			height: 200px;
			margin-top: 20px !important;
		}
		// @include m486_768 {
		// 	height: 200px;
		// 	margin-top: 20px !important;
		// }

		// @include m768_991 {
		// 	height: 320px;
		// 	margin-top: 20px !important;
		// }
		.grid_item__image {
			height: 575px;
			width: 100rem;
			// display: block;
			background: url(../img/children-all.webp) no-repeat left bottom;
			@include m991 {
				height: 200px;
				background-size: contain;
				width: 345px;
				background: url(../img/children-all_smart.webp) no-repeat left bottom;
			}
			// @include m486_768 {
			// 	height: 200px;
			// }

			// @include m768_991 {
			// 	height: 280px;
			// }
		}
		img {
			height: 540px;
			@include m991 {
				height: 200px;
				max-width: 100%;
				object-fit: cover;
				object-position: top center;
			}
		}
		.grid_item__abs {
			background-color: rgba(227, 6, 19, 0.8);
			box-shadow: 0 0 0 0.1px rgba(227, 6, 19, 0.8);
			font-size: 18px;
			text-transform: uppercase;
			@include m991 {
				width: 65px;
				height: 64px;
			}
			span {
				@include m991 {
					font-size: 12px;
					margin-top: 15px;
					margin-left: 11px;
				}
			}
		}
		@include m991 {
			order: 1;
		}
	}

	.item_3 {
		grid-area: item_3;
		.grid_item__abs {
			background-color: rgba(227, 6, 19, 0.8);
			box-shadow: 0 0 0 0.1px rgba(227, 6, 19, 0.8);
			span {
				@include m991 {
					margin-left: 5px;
				}
			}
		}
	}

	.item_4 {
		grid-area: item_4;
	}
	.grid_item {
		position: relative;
		overflow: hidden;
		z-index: 0;
		@include m486_768 {
			margin: 0 10px;
		}

		@include m768_991 {
			margin: 0 10px;
		}
		&:hover {
			opacity: 0.8;
			transition: all .5s;
		}
		&:not(:hover) {
			transition: all .5s;
		}
		&__abs {
			position: absolute;
			bottom: -28px;
			right: -20px;
			width: 105px;
			height: 105px;
			border-radius: 50%;
			display: flex;
			font-weight: bold;
			font-size: 36px;
			border: 2px dashed #FFFFFF;
			box-shadow: 0 0 0 0.1px rgba(1, 161, 154, 0.9);
			color: #ffffff;
			background-color: rgba(1, 161, 154, 0.9);
			@include m991 {
				width: 52px;
				height: 52px;
				font-size: 24px;
				bottom: -10px;
				right: -10px;
				font-weight: 500;
			}
			span {
				margin-top: 18px;
				margin-left: 20px;
				@include m991 {
					margin-top: 8px;
					margin-left: 8px;
				}
			}
		}
	}
}

.row_center {
	display: flex;
	justify-content: center;
}


.unset-mt {
	margin-top: 0 !important;
}

.parallax_w1,
.parallax_w2,
.parallax_w3 {
	position: absolute;
}

.parallax_w1 {
	bottom: 0;
	left: 0px;
}

.parallax_w2 {
	left: -280px;
	bottom: 215px;
	transform: rotate(45deg) !important;
}

.parallax_w3 {
	bottom: 655px;
	left: -260px;
	transform: scale(-1,1);

}